<template>
<div id="mainPage">
  <div class="header">
    <img style="margin-right: 10px" src="../../assets/images/logo.png" alt="">
    <div>小月智慧环卫云平台</div>
    <div class="header-nav">
      <!--<div @click="goPath(item, index)" v-for="(item, index) in headerNav" :key="index" :style="bac" :class="navClass">-->
        <!--{{item}}-->
      <!--</div>-->
      <div  @click="goPath('/mian', 1)" class="nav-item" :class="{'nav-item-active': navIndex === 1}">首页</div>
      <div  @click="goPath('/mian', 2)" class="nav-item" :class="{'nav-item-active': navIndex === 2}">人员管理</div>
      <div  @click="goPath('/mian', 3)" class="nav-item" :class="{'nav-item-active': navIndex === 3}">实时监控</div>
      <div  @click="goPath('/mian', 4)" class="nav-item" :class="{'nav-item-active': navIndex === 4}">警报处理</div>
    </div>
  </div>
  <div class="content">
    <div class="nav_left1">
      <el-menu :default-active="navLeft" style="width: 100%;background-color: #EFF4F5;border-right: 0;"  class="el-menu-vertical-demo"  :collapse="isCollapse">

        <el-menu-item @click.native="goPath2('/main', '1')"  index="1">
          <!--<i class="el-icon-menu"></i>-->
          <img v-if="navLeft==='1'" src="../../assets/images/home1.png" alt="">
          <img v-if="navLeft!=='1'" src="../../assets/images/home2.png" alt="">
          <span slot="title">主页</span>
        </el-menu-item>
        <el-menu-item @click.native="goPath2('/main', '2')" index="2">
          <img v-if="navLeft==='2'" src="../../assets/images/user1.png" alt="">
          <img v-if="navLeft!=='2'" src="../../assets/images/user2.png" alt="">
          <span slot="title">人员信息</span>
        </el-menu-item>
        <el-menu-item @click.native="goPath2('/main', '3')" index="3">
          <img v-if="navLeft==='3'" src="../../assets/images/biao1.png" alt="">
          <img v-if="navLeft!=='3'" src="../../assets/images/biao2.png" alt="">
          <span slot="title">智能手表</span>
        </el-menu-item>
        <el-menu-item @click.native="goPath2('/main', '4')" index="4">
          <img v-if="navLeft==='4'" src="../../assets/images/date1.png" alt="">
          <img v-if="navLeft!=='4'" src="../../assets/images/date2.png" alt="">
          <span slot="title">日常考勤</span>
        </el-menu-item>
        <el-menu-item @click.native="goPath2('/main', '5')" index="5">
          <img v-if="navLeft==='5'" src="../../assets/images/map1.png" alt="">
          <img v-if="navLeft!=='5'" src="../../assets/images/map2.png" alt="">
          <span slot="title">电子围栏</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div style="flex: 1;height: 100%;">
      <router-view></router-view>
    </div>
  </div>
</div>
</template>

<script>
  export default {
    data() {
      return {
        center: {lng: 0, lat: 0},
        zoom: 3,
        scrollWheelZoom: true,
        isCollapse: true,
        headerNav: ['首页', '人员管理', '实时监控', '警报处理'],
        navClass: {
          'nav-item': true,
          'nav-item-active': false
        },
        navIndex: 1,
        navLeft: '1'
      }
    },
    created() {
      let token = localStorage.getItem('token')
      let UserId = localStorage.getItem('UserId')
      token ? this.$store.commit('getToken', token) : this.$store.commit('token', null)
      UserId ? this.$store.commit('getUserId', UserId) : this.$store.commit('getUserId', null)
      this.getUserInfo()
      this.$cookie.setCookie('login', 'asda', 1)
      console.log(this.$cookie.getCookie('login'))
    },
    methods: {
      getUserInfo() {
        this.$api.getUserInfo({UserId: this.userId}).then((res) => {
          console.log(res)
        })
      },
      handler({BMap, map}) {
        console.log(BMap, map)
        this.center.lng = 116.404
        this.center.lat = 39.915
        this.zoom = 15
      },
      goPath(path, index) {
     //   this.$router.push(path)
        this.navIndex = index
      },
      goPath2(path, index) {
        //   this.$router.push(path)
        this.navLeft = index
      },
      selectLeft(val) {
        console.log(val)
      }
    },
    computed: {
      userId() {
        return this.$store.state.UserId
      }
    }
  }
</script>

<style lang="less" scoped>
  @import (reference) "../../style/mixin";
  #mainPage{
    .flex();
    height: 100%;
    flex-direction: column;
    .header{
      height: 60px;
      width: 100%;
      background-image: linear-gradient(to right, rgba(64,147,84,0.86) 0%, rgba(17,154,150,0.86) 71%, rgba(17,144,212,0.86) 100%);
      .font(17px, #fff);
      .flex(@justify-content: flex-start);
      padding-left: 70px;
      letter-spacing: 2px;
      .header-nav{
        padding-left: 190px;
        height: 100%;
        .flex(@justify-content: flex-start);
        .nav-item{
          padding: 0 20px;
          cursor: pointer;
          .font(16px, #fff);
          text-align: center;
          height: 100%;
          .flex()
        }
        .nav-item:hover{
          background-color: #93C172;
        }
        .nav-item-active{
          background-color: #93C172;
        }
      }
    }
    .content{
      flex: 1;
      height: 100%;
      width: 100%;
      .flex();
      .nav_left1{
        width: 57px;
        height: 100%;
        background-color: #EFF4F5;
        .el-menu-item.is-active{
          border-right: 2px solid #32d17a;
          background-color: #E2EAEC;
        }
        .el-menu-item, .el-submenu__title{
          height: 52px;
          line-height: 52px;
        }
      }
    }
  }
</style>



